import { observer } from 'mobx-react-lite';
import cl from './ChatContainer.module.css'
import { useState, useRef } from 'react';
import chatStore from '../../../store/chatStore';
import Messages from './messages/Messages';
import axios from 'axios';

//  ChatContainer,   observer   
const ChatContainer: React.FC = observer(() => {
    const [value, setValue] = useState<string>(''); //    
    const textAreaRef = useRef<HTMLTextAreaElement>(null); //    textarea

    //     textarea
    const handleInputChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
        setValue(event.target.value); //   

        //       textarea
        // if (textAreaRef.current) {
        //     textAreaRef.current.style.height = 'auto';
        //     textAreaRef.current.style.height = `${Math.min(textAreaRef.current.scrollHeight, 200)}px`;
        // }
    };

    //   
    const handleSendMessage = async () => {
        if (value.trim()) { //    
            //      
            chatStore.addMessage(value);

            setValue(''); //   
            if (textAreaRef.current) {
                textAreaRef.current.style.height = 'auto'; //   textarea
            }

            try {
                const payload = {
                    prompt: `  ,     : ${value}`, //     
                    milvus_prompt: `    - ${value},   `
                };                
                const response = await axios.post('http://192.168.88.223:5555/report', payload); //  POST-
                
                //    
                if (response.data && response.data.reply) {
                    chatStore.addResponse(); //     
                }
            } catch (error) {
                console.error("Error sending message:", error); //  
            }
        }
    };

    //     textarea
    const handleKeyDown = (event: React.KeyboardEvent<HTMLTextAreaElement>) => {
        if (event.key === 'Enter') { //    Enter
            if (event.ctrlKey) { //   Ctrl,   
                return;
            } else {
                event.preventDefault(); //    Enter
                handleSendMessage(); //  
            }
        }
    };

    return (
        <>
            <div className={`${cl.chatContainer} ${chatStore.isOpen ? '' : cl.chatContainerClosed}`} >
                <div className={cl.tittleContainer}> {/*     */}
                    <button className={cl.closeChat} onClick={() => { chatStore.handleCloseChat() }}> {/*    */}
                        
                    </button>
                    <div className={cl.tittle}> {/*   */}
                        AI-
                    </div>
                </div>

                <div className={cl.textChatContainer}> {/*     */}
                    <Messages /> {/*     */}
                </div>

                <div className={cl.inputContainer}> {/*     */}
                    <div className={cl.inputButtonContainer}> {/*   textarea   */}
                        <textarea
                            ref={textAreaRef} //   textarea
                            placeholder={' '} // 
                            className={cl.input} // CSS-  textarea
                            value={value} //  textarea
                            onKeyDown={handleKeyDown} //   
                            onChange={handleInputChange} //   
                            rows={1} //   
                        />
                        <button className={cl.buttonForInput} onClick={handleSendMessage}> {/*    */}
                            
                        </button>
                    </div>
                </div>
            </div>

            <button
                className={`${cl.openChat} ${chatStore.isHideCloseChatButton ? cl.openChatIsOpen : ''}`} //    
                onClick={() => chatStore.handleOpenChat()}
            >
                AI-
            </button>
        </>
    )
})

export default ChatContainer