import { makeObservable, observable, action } from "mobx"; //   MobX      

//     
interface Message {
    text: string; //  
    isUser: boolean; // , ,     
}

//     
class ChatStore {
    isOpen: boolean; //   (  )
    isHideCloseChatButton: boolean; // ,      

    messages: Message[] = []; //   
    responseIndex: number = 1; //     

    constructor() {
        this.isOpen = false; //   
        this.isHideCloseChatButton = false; //     

        //        MobX
        makeObservable(this, {
            isOpen: observable, //  
            isHideCloseChatButton: observable, //  
            messages: observable.deep, //    (  )
            responseIndex: observable, 
            
            handleCloseChat: action, //    
            handleOpenChat: action, //    
            addMessage: action, //    
            addResponse: action, //                
        });
    }

    //    
    handleCloseChat() {
        this.isOpen = false; //     
        setTimeout(() => {
            this.isHideCloseChatButton = false; //     500 
        }, 500);
    }

    //    
    handleOpenChat() {
        this.isOpen = true; //     
        this.isHideCloseChatButton = true; //   
    }

    addResponse() {
        const responseText = `    ${this.responseIndex}`;
        this.messages.push({ text: responseText, isUser: false });
        this.responseIndex += 1; //     
    }

    //     
    addMessage(message: string) {
        this.messages.push({ text: message, isUser: true }); //    
        this.addResponse();
    }
}


const chatStore = new ChatStore();
export default chatStore; 
